html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: none;
}

body {
  background: #FFF;
  line-height: 1.2;
  font-size: 14px;
  font-family: "Lora", Arial, Helvetica, serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

button {
  background: none;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.upper {
  text-transform: uppercase;
}

.container {
  max-width: 1590px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
}

.l {
  display: flex;
  flex-flow: row nowrap;
}

.l_stretch {
  align-items: stretch;
}

.l_center {
  align-items: center;
}

.l_between {
  justify-content: space-between;
}

.l_around {
  justify-content: space-around;
}

.l__col {
  flex: 1 0 auto;
}

.header__top {
  padding: 10px 0;
  border-bottom: 1px solid #6ecdbe;
  overflow: hidden;
}

.header__link {
  display: inline-block;
  padding: 0 40px;
  font-size: 11px;
  text-transform: uppercase;
  color: #303d4a;
  line-height: 1.9;
  transition: opacity 0.2s;
  font-family: "Ubuntu", Arial, Helvetica, serif;
  font-weight: 300;
}

.header__link:not(:last-child) {
  border-right: 1px solid #6ecdbe;
}

.header__link:last-child {
  margin-right: -40px;
}

.header__link:hover {
  opacity: 0.7;
}

.footer {
  background: #f08058;
  padding: 29px 0;
}

.footer__item {
  display: inline-block;
  font-size: 13px;
  line-height: 1;
  color: #fff;
  font-family: "Ubuntu", Arial, Helvetica, serif;
  font-weight: 300;
}

.footer__item:not(.upper) {
  max-width: 140px;
}

.footer__item:not(div) {
  transition: opacity 0.15s;
}

.footer__item:not(div):hover {
  opacity: .8;
}

.logo {
  font-size: 32px;
  line-height: 40px;
  color: #303d4a;
  font-family: "Lora", Arial, Helvetica, serif;
  font-weight: 400;
  font-style: italic;
}

.logo > span {
  color: #f08058;
}

.nav {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
  margin: 0 -40px;
}

.nav__link {
  display: inline-block;
  font-size: 13px;
  color: #303d4a;
  margin: 0 40px;
  line-height: 100px;
  text-transform: uppercase;
  border-bottom: 1px solid transparent;
  transition: border-bottom-color 0.2s ease-in-out;
  font-family: "Ubuntu", Arial, Helvetica, serif;
  font-weight: 300;
}

.nav__link:hover {
  border-bottom-color: #6ecdbe;
}

.btn {
  display: inline-block;
  vertical-align: middle;
  font-size: 18px;
  border-radius: 24px;
  border: 1px solid transparent;
  text-align: center;
  padding: 12px 23px 13px;
  transition: background .2s, border-color .2s;
  font-family: "Lora", Arial, Helvetica, serif;
  font-weight: 400;
  font-style: italic;
}

.btn_width {
  max-width: 100%;
}

.btn_full-orange {
  background: #f08058;
  color: #fff;
}

.btn_full-orange:hover {
  background: #e46b40;
}

.btn_full-green {
  background: #6ecdbe;
  color: #fff;
}

.btn_full-white {
  background: #fff;
  color: #f08058;
}

.btn:disabled {
  background: gray;
  color: #fff;
}

.btn_border-orange {
  border-bottom-color: #f08058;
  color: #f08058;
}

.btn_border-white {
  border-bottom-color: #fff;
  color: #fff;
}

.btn_border-green {
  border-bottom-color: #6ecdbe;
  color: #6ecdbe;
}

.btn_md {
  font-size: 22px;
  padding: 10px 20px;
}
