// fonts
@mixin lora {
  font-family: "Lora", Arial, Helvetica, serif;
  font-weight: 400; }
@mixin lora-i {
	font-family: "Lora", Arial, Helvetica, serif;
	font-weight: 400;
	font-style: italic; }
@mixin lora-bi {
	font-family: "Lora", Arial, Helvetica, serif;
	font-weight: 700;
	font-style: italic; }
@mixin lora-b {
  font-family: "Lora", Arial, Helvetica, serif;
  font-weight: 700; }

@mixin ubuntu {
  font-family: "Ubuntu", Arial, Helvetica, serif;
  font-weight: 400; }
@mixin ubuntu-l {
  font-family: "Ubuntu", Arial, Helvetica, serif;
  font-weight: 300; }
@mixin ubuntu-b {
  font-family: "Ubuntu", Arial, Helvetica, serif;
  font-weight: 700; }

body {
	background: #FFF;
	line-height: 1.2;
	font-size: 14px;
	@include lora;
	@include smooth-font; }

a {
	text-decoration: none; }

button {
	background: none; }

.text-center {
	text-align: center; }

.text-right {
	text-align: right; }

.text-left {
	text-align: left; }

.upper {
	text-transform: uppercase; }
