.footer {
  background: $orange;
  padding: 29px 0;
  &__item {
    display: inline-block;
    font-size: 13px;
    line-height: 1;
    color: #fff;
    @include ubuntu-l;
    &:not(.upper) {
      max-width: 140px; }
    &:not(div) {
      @include tr(opacity .15s);
      &:hover {
        opacity: .8; } } } }
